.breed-select {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 20px;
  padding: 10px;
  font-size: 16px;
}

.cat-info {
  opacity: 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  gap: 20px;
  margin: 20px;
  padding: 20px;
  display: flex;
}

.cat-info:has( > :not(:empty)) {
  opacity: 1;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-radius: 50%;
  margin: 20px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 24px;
  background: #000;
  margin: 0 auto;
  position: absolute;
  bottom: -20px;
  left: 46px;
  transform: rotate(-45deg);
}

@keyframes animloader {
  0% {
    transform: translate(-10px, -10px);
  }

  25% {
    transform: translate(-10px, 10px);
  }

  50% {
    transform: translate(10px, 10px);
  }

  75% {
    transform: translate(10px, -10px);
  }

  100% {
    transform: translate(-10px, -10px);
  }
}

.error {
  color: red;
  margin: 20px;
}

#selectElement {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.cat {
  font-size: 32px;
}

.cat-text {
  font-size: 16px;
}

